$accent-color: #ffb400;
$font: "GreycliffCF";

html {
  font-size: 10px;
  font-family: $font, sans-serif;
}
body {
  color: #ffffff;
  font-size: 1.8rem;
  &.lock {
    overflow: hidden;
  }
}
.wrapper {
  &.blured {
    filter: blur(3px);
  }
}
.container {
  margin: 0 auto;
  max-width: 1170px;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.accent-text {
  color: $accent-color;
}
.extrabold {
  font-weight: 800;
}

.popup {
  &-wrapper {
    position: fixed;
    z-index: 20;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    &--hidden {
      display: none;
    }
  }
  padding: 60px 85px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: fixed;
  z-index: 20;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
  }
  &__input {
    text-align: center;
    width: 100%;
    padding: 22px;
    border: none;
    border-radius: 50px;
    margin-bottom: 25px;
    background-color: #f4f4f4;
    &::placeholder {
      font-family: 'Montserrat';
      color: #cdcdcd;
    }
  }
  &__btn {
    margin-top: 20px;
    box-shadow: 0px 4px 0px 0px #eb9500;
    width: 100%;
    border: none;
    padding: 25px 75px;
    color: #ffffff;
    font-weight: 800;
    background-color: #cfcfd2;
    background-image: linear-gradient(45deg, $accent-color 34%, #ffce6e 65%);
    border-radius: 50px;
    transition: .25s;
    cursor: pointer;
    &:hover {
      box-shadow: 0px 0px 0px 0px #eb9500;
      transform: translateY(4px);
    }
  }
  &__text {
    font-size: 1.2rem;
    font-weight: 300;
    color: #000000;
  }
  &__title {
    font-weight: 800;
    color: #000000;
    margin-bottom: 12px;
  }
  &__subtitle {
    font-size: 1.4rem;
    font-weight: 500;
    color: #000000;
    margin-bottom: 40px;
  }
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
    transform: skewX(-4deg);
    background: url(../img/bg/bg_popup.png) no-repeat top left / 100% auto;
    background-color: #fff;
    border-radius: 10px;
  }
}

.popup-contact {
  max-width: 480px;
}
.popup-catalog {
  max-width: 730px;
  padding-left: 300px;
  &::before {
    background-position: right top;
    background-size: auto;
    background-image:
      url(../img/bg/bg_popup.png),
      linear-gradient(45deg, #ffffff 9%, #d6e7f1 90%);
  }
  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
    background: url(../img/content/journal_big.png) no-repeat left center / auto;
  }
}